        if(date) {
            const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });
            const formattedDate = formatter.format(new Date(date));
            return formattedDate;
        }
    }
    function openManageOrganizationDialog () {
        const closeCb = (reason: "close" | "outclick" | "component") => {
            if(reason == "component") {
                getTagData();
            }
        }
        dialog.showDialog(
                defineAsyncComponent(() => import(`../../components/dialogs/backoffice/manageOrganization.vue`)),
                { closable: true, title: "Manage Organization", width: "592px", maxHeight: "728px", closeCb },{organization: organization.value}
            );
        }
