    }
    function listOrganizationApps() {
        const organizationId = routerParams.organizationId;
        executor.execute(async () => {
            appList.value = await backofficeUseCase.listOrganizationApps(organizationId);
        }, { loading: true });
    }
    function viewUser(userId: string) {
        router.push(`/backoffice/user/${userId}`);
    }
    function getFilteredUsers() {
         return userList.value.filter((user) => (user.name?.toUpperCase().includes(searchUser.value.toUpperCase()) || 
                                                user.lastName?.toUpperCase().includes(searchUser.value.toUpperCase()) || 
                                                user.email?.toUpperCase().includes(searchUser.value.toUpperCase())) )
    }
